import { AtlasImagenes, AtlasImagenesDetalles, Imagenes, SpriteImagenes } from "../Enumeraciones/Diccionario";
import { configuracionLineasGanadoras } from "../Enumeraciones/exportFunction";

export class GrillaBolas {
	private escena: Phaser.Scene;
	private bolasGrupo: Phaser.GameObjects.Group;
	private tweenBolasEfectoBrillo: Phaser.Tweens.Tween;
	private circulos: {CirculoIzquierda: Phaser.GameObjects.Sprite, CirculoDerecha: Phaser.GameObjects.Sprite, linea: Phaser.GameObjects.Graphics } [] = [];
	private configuracionCirculos: { posIzquierdaX: number, posDerechaX: number, posicionY: number, listaBolas: number[] }[];

	constructor(escene: Phaser.Scene) {
		this.escena = escene;
		this.configuracionCirculos = configuracionLineasGanadoras();
		this.crearGrillaBolas();
		this.configurarCirculos();
	}

	private crearGrillaBolas() {
		const listaFrames = Phaser.Utils.Array.NumberArray(1, 49).map(x => AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(x));
		this.bolasGrupo = this.escena.make.group({
			key: AtlasImagenes.Bolas,
			frame: listaFrames,
			randomFrame: true,
			max: 15,
			gridAlign: {
				x: 100,
				y: 150,
				width: 5,
				height: 3,
				cellWidth: 100,
				cellHeight: 100,
			},
			"setScale.x": 0.5,
			"setScale.y": 0.5,
		});
		// console.log(this.bolasGrupo.children);
		// const lista = <Phaser.GameObjects.Sprite[]> this.bolasGrupo.getChildren();
		// const efecto = this.escena.add.image(lista[0].x, lista[0].y, Imagenes.Brillo).setDisplaySize(150, 150);
		// this.escena.tweens.add({
		// 	targets: efecto,
		// 	alpha: 1,
		// 	duration: 1000,
		// 	yoyo: true,
		// 	onStart: () => {
		// 		efecto.alpha = 0.1
		// 	},
		// 	onComplete: () => {
		// 		efecto.destroy();
		// 	}
		// });
		// console.log(lista[0].displayWidth);
	}

	configurarCirculos() {
		const callbackBrilloBolas = (listaBolas: number[]) => {
			// console.log(this.escena.tweens.getAllTweens());

			if (!this.tweenBolasEfectoBrillo?.isPlaying()) {
				const lista = <Phaser.GameObjects.Sprite[]> this.bolasGrupo.getChildren().filter((x, y) => listaBolas.includes(y));
				const efecto: Phaser.GameObjects.Image[] = [];
				lista.forEach(item => efecto.push(this.escena.add.image(item.x, item.y, Imagenes.Brillo).setDisplaySize(150, 150)));
				this.tweenBolasEfectoBrillo = this.escena.tweens.add({
					targets: efecto,
					alpha: 0.5,
					duration: 500,
					yoyo: true,
					onStart: () => {
						for (const iterator of efecto) {
							iterator.alpha = 0.1
						}
					},
					onComplete: () => {
						for (const iterator of efecto) {
							iterator.destroy();
						}
					}
				});
			}

			// const efecto = this.escena.add.image(lista[0].x, lista[0].y, Imagenes.Brillo).setDisplaySize(150, 150);
			// this.bolasEfectoBrillo = this.escena.tweens.add({
			// 	targets: efecto,
			// 	alpha: 1,
			// 	duration: 1000,
			// 	yoyo: true,
			// 	onStart: () => {
			// 		efecto.alpha = 0.1
			// 	},
			// 	onComplete: () => {
			// 		efecto.destroy();
			// 	}
			// });
		};
		this.configuracionCirculos.forEach((element, index) => {
			this.circulos.push({
				CirculoIzquierda: this.escena.add.sprite(element.posIzquierdaX, element.posicionY, SpriteImagenes.Circulos, 0)
					.setInteractive({ useHandCursor: true })
					.on(Phaser.Input.Events.POINTER_MOVE, () => {
						this.circulos[index].CirculoIzquierda.setTexture(SpriteImagenes.Circulos, 1);
						this.circulos[index].CirculoDerecha.setTexture(SpriteImagenes.Circulos, 1);
						this.circulos[index].linea.visible = true;
						this.escena.tweens.add({
							targets: this.circulos[index].linea,
							alpha: 1,
							duration: 400,
							onStart: () => {
								this.circulos[index].linea.alpha = 0.2
							}
						});
						callbackBrilloBolas(element.listaBolas);
					})
					.on(Phaser.Input.Events.POINTER_OUT, () => {
						this.circulos[index].CirculoIzquierda.setTexture(SpriteImagenes.Circulos, 0);
						this.circulos[index].CirculoDerecha.setTexture(SpriteImagenes.Circulos, 0);
						this.escena.tweens.add({
							targets: this.circulos[index].linea,
							alpha: 0.2,
							duration: 400,
							onStart: () => {
								this.circulos[index].linea.alpha = 1;
							},
							onComplete: () => {
								this.circulos[index].linea.visible = false;
							}
						});

					}),
				CirculoDerecha: this.escena.add.sprite(element.posDerechaX, element.posicionY, SpriteImagenes.Circulos, 0)
					.setInteractive({ useHandCursor: true })
					.on(Phaser.Input.Events.POINTER_MOVE, () => {
						this.circulos[index].CirculoDerecha.setTexture(SpriteImagenes.Circulos, 1);
						this.circulos[index].CirculoIzquierda.setTexture(SpriteImagenes.Circulos, 1);
						this.circulos[index].linea.visible = true;
						this.escena.tweens.add({
							targets: this.circulos[index].linea,
							alpha: 1,
							duration: 400,
							onStart: () => {
								this.circulos[index].linea.alpha = 0.2
							}
						});
						callbackBrilloBolas(element.listaBolas);
					})
					.on(Phaser.Input.Events.POINTER_OUT, () => {
						this.circulos[index].CirculoDerecha.setTexture(SpriteImagenes.Circulos, 0);
						this.circulos[index].CirculoIzquierda.setTexture(SpriteImagenes.Circulos, 0);
						this.escena.tweens.add({
							targets: this.circulos[index].linea,
							alpha: 0.2,
							duration: 400,
							onStart: () => {
								this.circulos[index].linea.alpha = 1
							},
							onComplete: () => {
								this.circulos[index].linea.visible = false;
							}
						});
					}),
				linea: this.crearLinea(element.posIzquierdaX, element.posicionY, element.posDerechaX, element.posicionY, element.listaBolas)
			})
		});
	}

	crearLinea(inicioX: number, inicioY: number, finX: number, finY: number, listaBolasMarcar: number[]) {
		const linea = this.escena.add.graphics();
		linea.lineStyle(8, 0x80effc, 1);
		linea.visible = false;
		linea.strokeCircle(inicioX, inicioY, 4);
		linea.strokeCircle(finX, finY, 4);
		linea.lineStyle(5, 0x80effc, 1);

		// linea.strokeCircleShape(new Phaser.Geom.Circle(inicioX, inicioY, 4));
		// linea.strokeCircleShape(new Phaser.Geom.Circle(finX, finY, 4));
		const listaBolasGrilla = <Phaser.GameObjects.Sprite[]> this.bolasGrupo.getChildren();
		let nuevoPuntoX = 0, nuevoPuntoY = 0;
		for (const iterator of listaBolasMarcar) {
			nuevoPuntoX = listaBolasGrilla[iterator].x;
			nuevoPuntoY = listaBolasGrilla[iterator].y;
			linea.strokeLineShape(new Phaser.Geom.Line(inicioX, inicioY, nuevoPuntoX, nuevoPuntoY));
			inicioX = nuevoPuntoX;
			inicioY = nuevoPuntoY;
		}
		linea.strokeLineShape(new Phaser.Geom.Line(inicioX, inicioY, finX, finY));
		return linea;
	}
}