import { reproducirSonidoBotones } from "../Enumeraciones/exportFunction";
import { Imagenes, Variables, AtlasImagenes, AtlasImagenesDetalles } from "./../Enumeraciones/Diccionario";

export class EstrellaSeleccionar {
	private escena: Phaser.Scene;
	private botonMas: Phaser.GameObjects.Image;
	private botonMenos: Phaser.GameObjects.Image;
	private estrella: Phaser.GameObjects.Image;
	// private bola: Phaser.GameObjects.Sprite;
	// private textoBola: Phaser.GameObjects.Text;
	private grupoNumeros: Phaser.GameObjects.Group;

	private origenX: number;
	private origenY: number;
	constructor(escene: Phaser.Scene, origenX: number, origenY: number) {
		this.escena = escene;
		this.origenX = origenX;
		this.origenY = origenY;
		this.crear();
		this.posicionarNumeroSeleccionado();
	}

	private crear() {

		const callbackCambiarNumero = (incrementar: boolean) => {

			let numeroEstrella = <number> this.escena.registry.get(Variables.EstrellaSeleccionada);
			numeroEstrella += (incrementar ? 1 : -1);
			if (numeroEstrella <= 0) {
				numeroEstrella = 15;
			} else if (numeroEstrella >= 16) {
				numeroEstrella = 1;
			}
			this.escena.registry.set(Variables.EstrellaSeleccionada, numeroEstrella);
			this.posicionarNumeroSeleccionado(true, incrementar);
		};

		this.botonMas = this.escena.add.image(this.origenX, this.origenY, Imagenes.SeleccionMas)
			.setDepth(1)
			.setScale(0.5)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			})
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoBotones(this.escena);
				callbackCambiarNumero(true);
			});

		this.botonMenos = this.escena.add.image(this.origenX, this.origenY + 200, Imagenes.SeleccionMenos)
			.setDepth(1)
			.setScale(0.5)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			})
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoBotones(this.escena);
				callbackCambiarNumero(false);
			});

		this.estrella = this.escena.add.image(this.origenX, this.origenY + 100, Imagenes.Estrella).setScale(0.5);
		// this.grupoNumeros = this.escena.add.group(null, {
		// 	key: AtlasImagenes.Numeros,
		// 	repeat: 1,
		// 	frame: AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(5),
		// 	setXY: {
		// 		x: 800,
		// 		y: 250,
		// 		stepX: 50
		// 	},
		// });
		const decena = this.escena.add.sprite(this.origenX - 6, this.origenY + 100, AtlasImagenes.Numeros, AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(1)).setDisplaySize(12, 19);
		const unidad = this.escena.add.sprite(this.origenX + 6, this.origenY + 100, AtlasImagenes.Numeros, AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(5)).setDisplaySize(12, 19);

		this.grupoNumeros = this.escena.add.group();
		this.grupoNumeros.add(decena);
		this.grupoNumeros.add(unidad);

		// const texturaEstrella = this.escena.make.renderTexture({ width: this.estrella.width, height: this.estrella.height }, false);
		// texturaEstrella.draw(Imagenes.Estrella, 0, 0);
		// texturaEstrella.drawFrame(AtlasImagenes.Numeros, AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(1), texturaEstrella.width / 2 , texturaEstrella.height / 2);
		// texturaEstrella.saveTexture('texturEstrella');
		// this.escena.add.image(100, 50, 'texturEstrella').setScale(0.5);
	}

	private posicionarNumeroSeleccionado(incluirEfecto = false, incrementar = false) {
		const callbackPosicionarNumeros = () => {
			const numeroEstrella = <number> this.escena.registry.get(Variables.EstrellaSeleccionada);
			const listaHijos = <Phaser.GameObjects.Sprite[]> this.grupoNumeros.getChildren();
			listaHijos[1].visible = true;
			if (numeroEstrella < 10) {
				listaHijos[0].visible = false;
				listaHijos[1].setPosition(this.origenX, this.origenY + 100);
				listaHijos[1].setFrame(AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(numeroEstrella));
			} else {
				listaHijos[0].visible = true;
				listaHijos[1].setPosition(this.origenX + 6, this.origenY + 100);
				listaHijos[1].setFrame(AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(numeroEstrella - 10));
			}
		};
		if (incluirEfecto) {
			const listaNumeros = <Phaser.GameObjects.Sprite[]> this.grupoNumeros.getChildren();
			listaNumeros[0].visible = false;
			listaNumeros[1].visible = false;
			this.escena.tweens.add({
				targets: this.estrella,
				duration: 200,
				displayWidth: 0,
				displayHeight: 0,
				alpha: 0.1,
				y: incrementar ? this.origenY + 20 : this.origenY + 180,
				onStart: () => {
				//
				},
				onComplete: () => {
					this.escena.tweens.add({
						targets: this.estrella,
						alpha: 1,
						displayWidth: 90,
						displayHeight: 90,
						duration: 200,
						y: this.origenY + 100,
						onStart: () => {
							this.estrella.y = !incrementar ? this.origenY + 20 : this.origenY + 180;
						},
						onComplete: () => {
							callbackPosicionarNumeros();
						}
					});
				}
			});
		} else {
			callbackPosicionarNumeros();
		}
	}
}
