import { Eventos, Imagenes, Sonidos, SpriteImagenes, TexturasManuales, Variables } from "../Enumeraciones/Diccionario";
import { reproducirSonidoBotones } from "../Enumeraciones/exportFunction";
import { Idiomas, ObtenerTextoMultiIdioma, TextosAplicacion } from "../Enumeraciones/MultiIdioma";

export default class ConfiguracionesScene extends Phaser.Scene {
	public static Name = "ConfiguracionesScene";

	private fondoImagen: Phaser.GameObjects.Image;
	private cerrar: Phaser.GameObjects.Image;
	private textoIdioma: Phaser.GameObjects.Text;

	private musicaTexto: Phaser.GameObjects.Text;
	private musicaBoton: Phaser.GameObjects.Image;
	private musicaBotonMenos: Phaser.GameObjects.Image;
	private musicaBotonMas: Phaser.GameObjects.Image;
	private musicaTextoVolumen: Phaser.GameObjects.Text;
	private musicaVolumen: number;

	private musicaEfectosTexto: Phaser.GameObjects.Text;
	private musicaEfectosBoton: Phaser.GameObjects.Image;
	private musicaEfectosBotonMenos: Phaser.GameObjects.Image;
	private musicaEfectosBotonMas: Phaser.GameObjects.Image;
	private musicaEfectosTextoVolumen: Phaser.GameObjects.Text;
	private musicaEfectosVolumen: number;

	private opcionesIdioma: Phaser.GameObjects.Graphics[];
	// private listaIdiomas: Idiomas[] = [Idiomas.Espanol, Idiomas.Ingles, Idiomas.Portugues];

	public init() {
		this.opcionesIdioma = [];
		// por implementar
	}

	public preload(): void {
		// por implementar

	}

	public create(): void {
		this.add.image(0, 0, TexturasManuales.FondoTransparente)
			.setOrigin(0, 0)
			.setInteractive()
			.on(Phaser.Input.Events.POINTER_UP, () => {
				// if (!this.cerrandoEscena) {
				// 	this.cerrarEscena();
				// }
			});

		this.fondoImagen = this.add.image(this.cameras.main.centerX - 200, this.cameras.main.centerY - 125, Imagenes.ContenedorAcercaDe)
			.setOrigin(0, 0)
			.setDisplaySize(400, 250);
		// this.cerrar = this.add.image(this.fondoImagen.x + (this.fondoImagen.displayWidth / 2), this.fondoImagen.y - (this.fondoImagen.displayHeight / 2), Imagenes.Cerrar)
		this.cerrar = this.add.image(this.fondoImagen.x + 360, this.fondoImagen.y + 20, Imagenes.Cerrar)
			.setScale(0.5)
			.setOrigin(0, 0)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			})
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoBotones(this);
				this.scene.resume(this.registry.get(Variables.EscenaLaunchMenu));
				this.scene.stop(ConfiguracionesScene.Name);
			});
		this.crearObjetosIdiomas();
		this.crearObjetosVolumenMusica();
		this.crearObjetosVolumenEfectos();
	}

	crearObjetosIdiomas() {
		this.textoIdioma = this.add.text(this.fondoImagen.x + 50, this.fondoImagen.y + 50, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.ConfiguracionScene_SeleccionarIdioma))
			.setOrigin(0.5, 0)
			.setAlign('center')
			.setFont('Arial Black')
			.setFontSize(16)
			.setFontStyle('bold')
			.setStroke('#000000', 6)
			.setFill('#7dceff');

		this.add.image(this.fondoImagen.x + 100, this.fondoImagen.y + 50, Imagenes.IdiomaChile)
			.setOrigin(0, 0)
			.setDisplaySize(32, 32)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				this.seleccionarIdioma(Idiomas.Espanol);
			});
		this.add.image(this.fondoImagen.x + 135, this.fondoImagen.y + 50, Imagenes.IdiomaEstadosUnidos)
			.setOrigin(0, 0)
			.setDisplaySize(32, 32)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				this.seleccionarIdioma(Idiomas.Ingles);
			});
		this.add.image(this.fondoImagen.x + 170, this.fondoImagen.y + 50, Imagenes.IdiomaBrasil)
			.setOrigin(0, 0)
			.setDisplaySize(32, 32)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				this.seleccionarIdioma(Idiomas.Portugues);
			});

		this.add.sprite(this.fondoImagen.x + 117, this.fondoImagen.y + 100, SpriteImagenes.Circulos)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				this.seleccionarIdioma(Idiomas.Espanol);
			});

		this.add.sprite(this.fondoImagen.x + 152, this.fondoImagen.y + 100, SpriteImagenes.Circulos)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				this.seleccionarIdioma(Idiomas.Ingles);
			});

		this.add.sprite(this.fondoImagen.x + 187, this.fondoImagen.y + 100, SpriteImagenes.Circulos)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				this.seleccionarIdioma(Idiomas.Portugues);
			});

		let graphics = this.add.graphics({ fillStyle: { color: 0x92d9f7 } });
		graphics.visible = false;
		let circulo1 = new Phaser.Geom.Circle(this.fondoImagen.x + 117, this.fondoImagen.y + 100, 7);
		graphics.fillCircleShape(circulo1);
		this.opcionesIdioma.push(graphics);

		graphics = this.add.graphics({ fillStyle: { color: 0x92d9f7 } });
		graphics.visible = false;
		circulo1 = new Phaser.Geom.Circle(this.fondoImagen.x + 152, this.fondoImagen.y + 100, 7);
		graphics.fillCircleShape(circulo1);
		this.opcionesIdioma.push(graphics);

		graphics = this.add.graphics({ fillStyle: { color: 0x92d9f7 } });
		graphics.visible = false;
		circulo1 = new Phaser.Geom.Circle(this.fondoImagen.x + 187, this.fondoImagen.y + 100, 7);
		graphics.fillCircleShape(circulo1);
		this.opcionesIdioma.push(graphics);

		this.opcionesIdioma[this.registry.get(Variables.IdiomaSeleccionado)].visible = true;
	}

	seleccionarIdioma(numeroIdioma: number) {
		reproducirSonidoBotones(this);
		this.registry.set(Variables.IdiomaSeleccionado, numeroIdioma);
		for (const iterator of this.opcionesIdioma) {
			iterator.visible = false;
		}
		this.opcionesIdioma[numeroIdioma].visible = true;
		this.registry.events.emit(Eventos.CambiarIdioma, this.registry.get(Variables.EscenaLaunchMenu));
	}

	crearObjetosVolumenMusica() {
		const yPositionObjetos = this.fondoImagen.y + 125;
		const xPositionObjetos = this.fondoImagen.x + 50;
		this.musicaTexto = this.add.text(xPositionObjetos, yPositionObjetos, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.ConfiguracionScene_VolumenMusica))
			.setOrigin(0.5, 0)
			.setAlign('center')
			.setFont('Arial Black')
			.setFontSize(16)
			.setFontStyle('bold')
			.setStroke('#000000', 6)
			.setFill('#7dceff');

		const fnSonidoActivo = () => {
			const sonido = this.sound.get(Sonidos.MusicaFondo);
			return sonido?.isPlaying;
		};
		this.musicaBoton = this.add.image(xPositionObjetos + 50, yPositionObjetos, Imagenes.Musica)
			.setDepth(2)
			.setDisplaySize(30, 30)
			.setOrigin(0, 0)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, () => {
				this.musicaBoton.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, () => {
				if (fnSonidoActivo()) {
					this.musicaBoton.clearTint();
				} else {
					this.musicaBoton.setTint(0xff504d);
				}
			});
		this.musicaBoton.on(Phaser.Input.Events.POINTER_UP, () => {
			const sonido = this.sound.get(Sonidos.MusicaFondo);
			if (!sonido) {
				this.sound.play(Sonidos.MusicaFondo, { loop: true });
			}else if (sonido?.isPlaying) {
				sonido.stop();
			} else {
				sonido.play({loop: true});
			}
		});
		if (!fnSonidoActivo()) {
			this.musicaBoton.setTint(0xff504d);
		}

		const callbackMusicaCambiarVolumen = (incrementar: boolean) => {
			if (this.musicaVolumen === 100 && incrementar) {
				return;
			} else if (this.musicaVolumen === 10 && !incrementar) {
				return;
			}

			this.musicaVolumen += (incrementar ? 10 : -10);
			this.musicaTextoVolumen.setText(`${this.musicaVolumen} %`);
			const sonido = this.sound.get(Sonidos.MusicaFondo);
			sonido.stop();
			sonido.play({loop: true, volume: this.musicaVolumen / 100});
		};
		this.musicaBotonMenos = this.add.image(xPositionObjetos + 125 , yPositionObjetos, Imagenes.SeleccionMenos)
			.setDisplaySize(26, 20)
			.setOrigin(0, 0)
			.setRotation(1.5708)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				if (fnSonidoActivo()) {
					this.setTint(0x00ff00);
				}
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			})
			.on(Phaser.Input.Events.POINTER_UP, () => {
				if (fnSonidoActivo()) {
					reproducirSonidoBotones(this);
					callbackMusicaCambiarVolumen(false);
				}
			});

		this.musicaBotonMas = this.add.image(xPositionObjetos + 195 , yPositionObjetos, Imagenes.SeleccionMas)
			.setDisplaySize(26, 20)
			.setOrigin(0, 0)
			.setRotation(1.5708)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				if (fnSonidoActivo()) {
					this.setTint(0x00ff00);
				}
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			})
			.on(Phaser.Input.Events.POINTER_UP, () => {
				if (fnSonidoActivo()) {
					reproducirSonidoBotones(this);
					callbackMusicaCambiarVolumen(true);
				}
			});

		const sonido = this.sound.get(Sonidos.MusicaFondo);
		if (!sonido) {
			this.musicaVolumen = 100;
		} else {
			this.musicaVolumen = (sonido as any).config.volume * 100;
		}
		this.musicaTextoVolumen = this.add.text(this.musicaBotonMenos.x + 5, yPositionObjetos + 3, `${this.musicaVolumen} %`)
			.setAlign('left')
			.setFont('Arial Black')
			.setFontSize(12)
			.setFontStyle('bold')
			.setStroke('#000000', 3)
			.setFill('#ffffff');

	}

	crearObjetosVolumenEfectos() {
		const yPositionObjetos = this.fondoImagen.y + 175;
		const xPositionObjetos = this.fondoImagen.x + 50;
		this.musicaEfectosTexto = this.add.text(xPositionObjetos, yPositionObjetos, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.ConfiguracionScene_VolumenEfectos))
			.setOrigin(0.5, 0)
			.setAlign('center')
			.setFont('Arial Black')
			.setFontSize(16)
			.setFontStyle('bold')
			.setStroke('#000000', 6)
			.setFill('#7dceff');

		const fnSonidoEfectosActivo = () => {
			return <boolean> this.registry.get(Variables.MusicaEfectosActivada);
		};
		this.musicaEfectosBoton = this.add.image(xPositionObjetos + 50, yPositionObjetos, (<boolean> this.registry.get(Variables.MusicaEfectosActivada) ? Imagenes.MusicaEfectos : Imagenes.MusicaEfectosMute))
			.setDepth(2)
			.setDisplaySize(30, 30)
			.setOrigin(0, 0)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, () => {
				this.musicaEfectosBoton.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, () => {
				if (<boolean> this.registry.get(Variables.MusicaEfectosActivada)) {
					this.musicaEfectosBoton.clearTint();
				} else {
					this.musicaEfectosBoton.setTint(0xff504d);
				}
			});
		this.musicaEfectosBoton.on(Phaser.Input.Events.POINTER_UP, () => {
			const efectosSonidoActivados = <boolean> this.registry.get(Variables.MusicaEfectosActivada);
			this.registry.set(Variables.MusicaEfectosActivada, !efectosSonidoActivados);

			if (!efectosSonidoActivados) {
				this.musicaEfectosBoton.clearTint();
				this.musicaEfectosBoton.setTexture(Imagenes.MusicaEfectos);
			} else {
				this.musicaEfectosBoton.setTint(0xff504d);
				this.musicaEfectosBoton.setTexture(Imagenes.MusicaEfectosMute);
			}
		});
		if (!(<boolean> this.registry.get(Variables.MusicaEfectosActivada))) {
			this.musicaEfectosBoton.setTint(0xff504d);
		}


		const callbackMusicaEfectosCambiarVolumen = (incrementar: boolean) => {
			if (this.musicaEfectosVolumen === 100 && incrementar) {
				return;
			} else if (this.musicaEfectosVolumen === 10 && !incrementar) {
				return;
			}
			this.musicaEfectosVolumen += (incrementar ? 10 : -10);
			this.registry.set(Variables.MusicaEfectosVolumen, this.musicaEfectosVolumen);
			this.musicaEfectosTextoVolumen.setText(`${this.musicaEfectosVolumen} %`);
		};
		this.musicaEfectosBotonMenos = this.add.image(xPositionObjetos + 125 , yPositionObjetos, Imagenes.SeleccionMenos)
			.setDisplaySize(26, 20)
			.setOrigin(0, 0)
			.setRotation(1.5708)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				if (fnSonidoEfectosActivo()) {
					this.setTint(0x00ff00);
				}
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				if (fnSonidoEfectosActivo()) {
					this.clearTint();
				}
			})
			.on(Phaser.Input.Events.POINTER_UP, () => {
				if (fnSonidoEfectosActivo()) {
					callbackMusicaEfectosCambiarVolumen(false);
					reproducirSonidoBotones(this);
				}

			});

		this.musicaEfectosBotonMas = this.add.image(xPositionObjetos + 195 , yPositionObjetos, Imagenes.SeleccionMas)
			.setDisplaySize(26, 20)
			.setOrigin(0, 0)
			.setRotation(1.5708)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				if (fnSonidoEfectosActivo()) {
					this.setTint(0x00ff00);
				}
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				if (fnSonidoEfectosActivo()) {
					this.clearTint();
				}
			})
			.on(Phaser.Input.Events.POINTER_UP, () => {
				if (fnSonidoEfectosActivo()) {
					callbackMusicaEfectosCambiarVolumen(true);
					reproducirSonidoBotones(this);
				}
			});

		this.musicaEfectosVolumen = <number> this.registry.get(Variables.MusicaEfectosVolumen);
		this.musicaEfectosTextoVolumen = this.add.text(this.musicaBotonMenos.x + 5, yPositionObjetos + 3, `${this.musicaEfectosVolumen} %`)
			.setAlign('left')
			.setFont('Arial Black')
			.setFontSize(12)
			.setFontStyle('bold')
			.setStroke('#000000', 3)
			.setFill('#ffffff');

	}

	cambioTextosIdioma() {
		this.textoIdioma.setText(ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.ConfiguracionScene_SeleccionarIdioma));
		this.musicaTexto.setText(ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.ConfiguracionScene_VolumenMusica));
		this.musicaEfectosTexto.setText(ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.ConfiguracionScene_VolumenEfectos));
	}
}