import { LluviaGotas } from '../Components/LluviaGotas';
import { Imagenes, Variables, AtlasImagenes, AtlasImagenesDetalles, SpriteImagenes, SpriteDetalles } from "./../Enumeraciones/Diccionario";
import { NumeroSeleccionado } from "../Components/NumeroSeleccionado";
import { fragmentShaderFuegosArtificiales, reproducirSonidoBotones, reproducirSonidoCambioEscena } from "./../Enumeraciones/exportFunction";
import SeleccionEscene from './SeleccionEscene';
import { GrillaBolas } from '../Components/GrillaBolas';
import FooterScene from './FooterScene';
import { ObtenerTextoMultiIdioma, TextosAplicacion } from '../Enumeraciones/MultiIdioma';

export default class MainGame extends Phaser.Scene {
	/**
     * Unique name of the scene.
     */
	public static Name = "MainGame";

	private lluviaGotasRectangulo: LluviaGotas;
	private grillaBolas: GrillaBolas;
	private botonConfiguracion: Phaser.GameObjects.Image;
	private botonLanzar: Phaser.GameObjects.Image;
	private estrella: Phaser.GameObjects.Image;
	private estrella1: Phaser.GameObjects.Image;
	private estrella2: Phaser.GameObjects.Image;

	private numerosSeleccionados: NumeroSeleccionado;
	private estrellaSeleccionada: Phaser.GameObjects.Image;
	private estrellasGrupo: Phaser.GameObjects.Group;
	private estrellasTween: Phaser.Tweens.Tween;

	private textoBotonLanzar: Phaser.GameObjects.Text;
	private lanzar = false;

	// private bolasGrupo: Phaser.GameObjects.Group;

	public init() {
		this.scene.launch(FooterScene.Name);
		this.lluviaGotasRectangulo = new LluviaGotas(this,
			{
				x: {min: 750, max: 900},
				y: {min: 100, max: 450},
				lifeSpan: 300,
				speedY: {min: 50, max: 100},
				cantidad: 5,
				vertical: true
			});
	}

	public preload(): void {
		// por implementar
	}

	public create(): void {
		this.cameras.main.fadeIn(1000, 0, 0, 0);
		const baseShader = new Phaser.Display.BaseShader('BufferShader2', fragmentShaderFuegosArtificiales());
		const shader = this.add.shader(baseShader, 0, 0, this.cameras.main.displayWidth, this.cameras.main.displayHeight).setOrigin(0, 0);
		this.add.image(0, 0, Imagenes.Background).setOrigin(0, 0).setSize(this.cameras.main.displayWidth, this.cameras.main.displayHeight).setAlpha(0.7);

		this.numerosSeleccionados = new NumeroSeleccionado(this);
		this.crearBotonConfig();

		this.lluviaGotasRectangulo.create();

		this.grillaBolas = new GrillaBolas(this);
		this.estrella = this.add.image(825, 160, Imagenes.Estrella).setScale(0.6);
		this.estrella1 = this.add.image(825, 280, Imagenes.Estrella).setScale(0.6);
		this.estrella2 = this.add.image(825, 400, Imagenes.Estrella).setScale(0.6);
		this.crearBotonLanzar();
		this.crearBotonCambiarNumeros();
		this.crearEstrellaSeleccionada();
	}

	public update ()
	{
		// if (this.estrellasGrupo) {
		// 	// Phaser.Actions.RotateAroundDistance(this.estrellasGrupo.getChildren(), { x: 400, y: 300 }, 0.02, this.estrellasTween.getValue());
		// 	Phaser.Actions.PlaceOnLine(this.estrellasGrupo.getChildren(), new Phaser.Geom.Line(1000, 0, 1000, 500))
		// }

		if (this.lanzar) {
			this.estrella.setPosition(825, this.estrella.y + 15)
			if (this.estrella.y >= 450) {
				this.estrella.y = 150;
			}
			this.estrella1.setPosition(825, this.estrella1.y + 15)
			if (this.estrella1.y >= 450) {
				this.estrella1.y = 150;
			}
			this.estrella2.setPosition(825, this.estrella2.y + 15)
			if (this.estrella2.y >= 450) {
				this.estrella2.y = 150;
			}
		}
	}

	crearBotonConfig() {
		this.botonConfiguracion = this.add.image(30, 30, Imagenes.MenuConfiguracion)
			.setScale(0.5)
			.setDepth(2)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoBotones(this);
				this.registry.set(Variables.EscenaLaunchMenu, MainGame.Name);
				this.scene.launch('MenuScene');
				this.scene.pause(MainGame.Name);
			})
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			});
	}

	crearBotonLanzar() {
		this.botonLanzar = this.add.image(980, 300, Imagenes.Comenzar)
			.setScale(0.5)
			.setDepth(2)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoBotones(this);
				this.lanzar = true;
				setTimeout(() => {
					this.lanzar = false;
					this.estrella.setPosition(825, 160)
					this.estrella1.setPosition(825, 280)
					this.estrella2.setPosition(825, 400)
				}, 5000);
			})
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			});

		this.textoBotonLanzar = this.add.text(980, 210, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.MainGameScene_BotonLanzar))
			.setOrigin(0.5, 0)
			.setFontFamily("monospace")
			.setFontSize(26)
			.setFill("#fff");

	}

	crearBotonCambiarNumeros() {
		this.botonLanzar = this.add.image(100, 10, Imagenes.CambiarNumeros)
			.setDisplaySize(50, 50)
			.setOrigin(0, 0)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoCambioEscena(this);
				this.cameras.main.fadeOut(500, 0, 0, 0);
				this.cameras.main.once(Phaser.Cameras.Scene2D.Events.FADE_OUT_COMPLETE, (cam, effect) => {
					this.scene.start(SeleccionEscene.Name);
				});
			})
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			});
	}

	crearEstrellaSeleccionada() {
		this.estrellaSeleccionada = this.add.image(510, 35, Imagenes.Estrella).setScale(0.3);
		const numeroEstrella = <number> this.registry.get(Variables.EstrellaSeleccionada);

		if (numeroEstrella < 10) {
			this.add.sprite(this.estrellaSeleccionada.x, this.estrellaSeleccionada.y, AtlasImagenes.Numeros, AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(numeroEstrella)).setDisplaySize(9, 14);
		} else {
			this.add.sprite(this.estrellaSeleccionada.x - 4, this.estrellaSeleccionada.y, AtlasImagenes.Numeros, AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(1)).setDisplaySize(9, 14);
			this.add.sprite(this.estrellaSeleccionada.x + 4, this.estrellaSeleccionada.y, AtlasImagenes.Numeros, AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(numeroEstrella - 10)).setDisplaySize(9, 14);
		}
	}

	cambioTextosIdioma() {
		this.textoBotonLanzar.setText(ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.MainGameScene_BotonLanzar))
	}
}
