import { reproducirSonidoBotones } from "../Enumeraciones/exportFunction";
import { Imagenes, Variables, AtlasImagenes, AtlasImagenesDetalles } from "./../Enumeraciones/Diccionario";

export class NumeroSeleccionar {
	private escena: Phaser.Scene;
	private botonMas: Phaser.GameObjects.Image;
	private botonMenos: Phaser.GameObjects.Image;
	// private bola: Phaser.GameObjects.Image;
	private bola: Phaser.GameObjects.Sprite;
	// private textoBola: Phaser.GameObjects.Text;
	private miGrupo: Phaser.GameObjects.Group;

	private indexNumeroSeleccionado: number;

	constructor(escene: Phaser.Scene, indexNumeroSeleccionado: number) {
		this.escena = escene;
		this.indexNumeroSeleccionado = indexNumeroSeleccionado;
		this.crear();
		this.posicionarNumeroSeleccionado();
	}

	private crear() {

		const callbackCambiarNumero = (incrementar: boolean) => {

			const listaNumerosSeleccionados = <number[]> this.escena.registry.get(Variables.NumerosSeleccionados);
			let nuevoNumero = listaNumerosSeleccionados[this.indexNumeroSeleccionado];
			do {
				nuevoNumero = nuevoNumero + (incrementar ? 1 : -1);
				if (nuevoNumero <= 0) {
					nuevoNumero = 49;
				} else if (nuevoNumero >= 50) {
					nuevoNumero = 1;
				}
			} while (listaNumerosSeleccionados.some(x => x === nuevoNumero));
			listaNumerosSeleccionados[this.indexNumeroSeleccionado] = nuevoNumero;
			this.escena.registry.set(Variables.NumerosSeleccionados, listaNumerosSeleccionados);
			this.posicionarNumeroSeleccionado(true, incrementar);
		};

		this.botonMas = this.escena.add.image(this.indexNumeroSeleccionado * 100 + 200, 200, Imagenes.SeleccionMas)
			.setDepth(1)
			.setScale(0.5)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			})
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoBotones(this.escena);
				callbackCambiarNumero(true);
			});

		this.botonMenos = this.escena.add.image(this.indexNumeroSeleccionado * 100 + 200, 400, Imagenes.SeleccionMenos)
			.setDepth(1)
			.setScale(0.5)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			})
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoBotones(this.escena);
				callbackCambiarNumero(false);
			});
	}

	private posicionarNumeroSeleccionado(incluirEfecto = false, incrementar = false) {
		const listaNumerosSeleccionados = this.escena.registry.get(Variables.NumerosSeleccionados);
		if (!this.bola) {
			this.bola = this.escena.add
				.sprite(this.botonMas.x, 300, AtlasImagenes.Bolas, AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(listaNumerosSeleccionados[this.indexNumeroSeleccionado]))
				.setDisplaySize(90, 90);
		} else if (!incluirEfecto) {
			this.bola.setTexture(AtlasImagenes.Bolas, AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(listaNumerosSeleccionados[this.indexNumeroSeleccionado]));
		}

		if	(incluirEfecto) {
			this.escena.tweens.add({
				targets: this.bola,
				duration: 200,
				displayWidth: 0,
				displayHeight: 0,
				alpha: 0.1,
				// y: incrementar ? 200 : 400,
				y: incrementar ? 230 : 370,
				onStart: () => {
					//
				},
				onComplete: () => {
					this.bola.setTexture(AtlasImagenes.Bolas, AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(listaNumerosSeleccionados[this.indexNumeroSeleccionado]));
					this.escena.tweens.add({
						targets: this.bola,
						alpha: 1,
						displayWidth: 90,
						displayHeight: 90,
						duration: 200,
						y: 300,
						onStart: () => {
							// this.bola.y = !incrementar ? 200 : 400;
							this.bola.y = !incrementar ? 230 : 370;
						}
					});
				}
			});
		}
	}

	refrescar() {
		this.bola.anims.play('explotar').on('animationcomplete', () => {
			this.posicionarNumeroSeleccionado();
		});
	}
}
