import { Variables } from "../Enumeraciones/Diccionario";
import { ObtenerTextoMultiIdioma, TextosAplicacion } from "../Enumeraciones/MultiIdioma";

export default class FooterScene extends Phaser.Scene {
	public static Name = "FooterScene";
	public horaTexto: Phaser.GameObjects.Text;
	public saldoTexto: Phaser.GameObjects.Text;
	public eventoTiempo: Phaser.Time.TimerEvent;
	// public costoPorLanzar: Phaser.GameObjects.Text;
	public init() {
		// por implementar
		// this.scene.moveDown();
	}

	public preload(): void {
		// por implementar

	}

	public create(): void {
		const graphics = this.add.graphics({ fillStyle: { color: 0x000000 } });
		const rect1 = new Phaser.Geom.Rectangle(0, this.cameras.main.height - 40, this.cameras.main.width, 40);
		graphics.fillRectShape(rect1);

		const horas = new Date().getHours();
		const minutos = new Date().getMinutes();
		this.horaTexto = this.add.text(0, 0, `: ${horas.toString().padStart(2, '0')}:${minutos.toString().padStart(2, '0')}`)
			.setOrigin(0, 0)
			.setAlign('center')
			.setFont('Arial Black')
			.setFontSize(24)
			.setFontStyle('bold')
			.setStroke('#000000', 6)
			.setFill('#ffffff');
		this.horaTexto.setPosition(this.cameras.main.width - this.horaTexto.displayWidth - 5, this.cameras.main.height - this.horaTexto.displayHeight - 5);

		const saldo = <number> this.registry.get(Variables.Saldo);
		const textoSaldo = ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.FooterScene_TextoSaldo);
		this.saldoTexto = this.add.text(0, 0, `${textoSaldo}: ${saldo.toLocaleString('es-cl')}`)
			.setOrigin(0, 0)
			.setAlign('center')
			.setFont('Arial Black')
			.setFontSize(24)
			.setFontStyle('bold')
			.setStroke('#000000', 6)
			.setFill('#ffffff');
		this.saldoTexto.setPosition(0, this.cameras.main.height - this.horaTexto.displayHeight - 5);

		this.eventoTiempo = this.time.addEvent({
			delay: 30000,
			repeat: -1,
			callback: () => {
				this.refrescarHora();
			}
		})
	}

	refrescarHora() {
		const horas = new Date().getHours();
		const minutos = new Date().getMinutes();
		this.horaTexto.setText(`: ${horas.toString().padStart(2, '0')}:${minutos.toString().padStart(2, '0')}`);
	}

	// mostrarCostoPorLanzar(costo: number) {
	// 	this.saldoTexto = this.add.text(200, 0, `Costo: ${costo.toLocaleString('es-cl')}`)
	// 		.setOrigin(0, 0)
	// 		.setAlign('center')
	// 		.setFont('Arial Black')
	// 		.setFontSize(24)
	// 		.setFontStyle('bold')
	// 		.setStroke('#000000', 6)
	// 		.setFill('#ffffff');
	// }

	cambioTextosIdioma() {
		const saldo = <number> this.registry.get(Variables.Saldo);
		const textoSaldo = ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.FooterScene_TextoSaldo);
		this.saldoTexto.setText(`${textoSaldo}: ${saldo.toLocaleString('es-cl')}`);
	}

}