import { EstrellaSeleccionar } from "../Components/EstrellaSeleccionar";
import { AtlasImagenes, AtlasImagenesDetalles, Imagenes, Variables } from "../Enumeraciones/Diccionario";
import { fragmentShaderFuegosArtificiales, numerosAzar, reproducirSonidoBotones, reproducirSonidoCambioEscena } from "../Enumeraciones/exportFunction";
import { ObtenerTextoMultiIdioma, TextosAplicacion } from "../Enumeraciones/MultiIdioma";
import FooterScene from "./FooterScene";
import MainGame from "./MainGame";
import MenuScene from "./MenuScene";
import SeleccionEscene from "./SeleccionEscene";

export default class SeleccionRapidaScene extends Phaser.Scene {
	public static Name = "SeleccionRapidaScene";
	private botonConfiguracion: Phaser.GameObjects.Image;
	private botonSeleccionNormal: Phaser.GameObjects.Image;
	private botonSeleccionAzar: Phaser.GameObjects.Image;
	private bolasGrupo: Phaser.GameObjects.Group;

	private estrellaSeleccionada: EstrellaSeleccionar;

	private textoBotonSeleccionNormal: Phaser.GameObjects.Text;
	private textoBotonAleatorio: Phaser.GameObjects.Text;
	private textoBotonComenzar: Phaser.GameObjects.Text;
	private textoSusNumeros: Phaser.GameObjects.Text;

	private listaBolasSeleccionadasImagenes: Phaser.GameObjects.Image[];
	private listaImagenesBrillo: Phaser.GameObjects.Image[];
	private listaBolasSeleccionadasIndices: number[];

	private puedeEliminarBolaSeleccionada: boolean;
	private puedeAgregarBolaSeleccionada: boolean;

	public init() {
		this.scene.launch(FooterScene.Name);
		this.listaBolasSeleccionadasImagenes = [];
		this.listaBolasSeleccionadasIndices = [];
		this.listaImagenesBrillo = [];
		this.puedeEliminarBolaSeleccionada = true;
		this.puedeAgregarBolaSeleccionada = true;
	}

	public preload(): void {
		// por implementar

	}

	public create(): void {
		this.cameras.main.fadeIn(1000, 0, 0, 0);
		const baseShader = new Phaser.Display.BaseShader('BufferShader2', fragmentShaderFuegosArtificiales());
		const shader = this.add.shader(baseShader, 0, 0, this.cameras.main.displayWidth, this.cameras.main.displayHeight).setOrigin(0, 0);
		this.add.image(0, 0, Imagenes.Background).setOrigin(0, 0).setSize(this.cameras.main.displayWidth, this.cameras.main.displayHeight).setAlpha(0.7);
		this.estrellaSeleccionada = new EstrellaSeleccionar(this, 830, 150);
		this.crearBotonConfig();
		this.crearGrillaBolas();
		this.crearGrillaNumerosSeleccionados();
		this.crearBotonSeleccionNormal();
		this.crearBotonSeleccionAzar();
		this.crearBotonComenzar();

		this.textoSusNumeros = this.add.text(100, 15, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.SeleccionRapidaScene_SusNumeros))
			.setOrigin(0, 0)
			.setFontFamily("monospace")
			.setFontSize(26)
			.setFill("#fff");
	}

	private crearGrillaNumerosSeleccionados() {
		let inicioX = 275;
		for (let index = 0; index < 5; index++) {
			const imagen = this.add.image(inicioX, 0, Imagenes.CirculoVacio)
				.setOrigin(0, 0)
				.setDisplaySize(60, 60);
			inicioX += 62;
			this.listaBolasSeleccionadasImagenes.push(imagen);
		}
	}

	private crearGrillaBolas() {
		const listaFrames = Phaser.Utils.Array.NumberArray(1, 49).map(x => AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(x));
		this.bolasGrupo = this.make.group({
			key: AtlasImagenes.Bolas,
			frame: listaFrames,
			max: 49,
			gridAlign: {
				x: 50,
				y: 50,
				width: 9,
				height: 6,
				cellWidth: 75,
				cellHeight: 75,
			},
			"setScale.x": 0.4,
			"setScale.y": 0.4,
		});

		this.bolasGrupo.getChildren().forEach((item: Phaser.GameObjects.Sprite, index) => {
			item.setInteractive({ useHandCursor: true })
				.on(Phaser.Input.Events.POINTER_UP, () => {
					if (this.puedeAgregarBolaSeleccionada && !this.listaBolasSeleccionadasIndices.includes(index)) {
						reproducirSonidoBotones(this);
						this.seleccionarNumero(item, index);
					}
				});
		});
		// this.bolasGrupo = this.add.group(null, {
		// 	key: AtlasImagenes.Bolas,
		// 	frame: listaFrames,
		// 	max: 49,
		// 	gridAlign: {
		// 		x: 50,
		// 		y: 50,
		// 		width: 9,
		// 		height: 6,
		// 		cellWidth: 75,
		// 		cellHeight: 75,
		// 	},
		// 	"setScale.x": 0.35,
		// 	"setScale.y": 0.35,
		// });
	}

	crearBotonConfig() {
		this.botonConfiguracion = this.add.image(30, 30, Imagenes.MenuConfiguracion)
			.setScale(0.5)
			.setDepth(2)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				this.botonConfiguracion.clearTint();
				reproducirSonidoBotones(this);
				this.registry.set(Variables.EscenaLaunchMenu, SeleccionRapidaScene.Name);
				this.scene.launch(MenuScene.Name);
				this.scene.pause(SeleccionRapidaScene.Name);
			})
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			});
	}

	crearBotonSeleccionNormal() {
		this.botonSeleccionNormal = this.add.image(0, 0, Imagenes.BotonGrande)
			.setScale(0.5)
			.setOrigin(0, 0)
			.setDepth(2)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoCambioEscena(this);
				this.cameras.main.fadeOut(500, 0, 0, 0);
				this.cameras.main.once(Phaser.Cameras.Scene2D.Events.FADE_OUT_COMPLETE, (cam, effect) => {
					this.scene.start(SeleccionEscene.Name);
				});
			})
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			});
		this.botonSeleccionNormal.setPosition(this.cameras.main.width - this.botonSeleccionNormal.displayWidth - 5, 0);

		this.textoBotonSeleccionNormal = this.add.text(this.botonSeleccionNormal.x + (this.botonSeleccionNormal.displayWidth / 2), 10, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.SeleccionRapidaScene_BotonSeleccionNormal))
			.setOrigin(0.5, 0)
			.setFontFamily("monospace")
			.setFontSize(26)
			.setFill("#fff");
	}

	crearBotonSeleccionAzar() {
		this.botonSeleccionAzar = this.add.image(400, 550, Imagenes.BotonGrande)
			.setScale(0.5)
			.setDepth(2)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoBotones(this);
				const listaNumeros = numerosAzar();
				this.seleccionarNumerosAzar();
			})
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			});

		this.textoBotonAleatorio = this.add.text(400, 535, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.SeleccionRapidaScene_BotonAleatorio))
			.setOrigin(0.5, 0)
			.setFontFamily("monospace")
			.setFontSize(26)
			.setFill("#fff");
	}

	crearBotonComenzar() {
		const callbackComprobarNumerosSeleccionados = () => {
			return this.listaBolasSeleccionadasIndices.length === 5;
		};
		this.botonSeleccionAzar = this.add.image(1000, 250, Imagenes.Comenzar)
			.setScale(0.5)
			.setDepth(2)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				if (callbackComprobarNumerosSeleccionados()) {
					reproducirSonidoCambioEscena(this);
					this.registry.set(Variables.NumerosSeleccionados, this.listaBolasSeleccionadasIndices.map(x => x + 1));
					this.cameras.main.fadeOut(500, 0, 0, 0);
					this.cameras.main.once(Phaser.Cameras.Scene2D.Events.FADE_OUT_COMPLETE, (cam, effect) => {
						this.scene.start(MainGame.Name);
					});
				}
			})
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				if (callbackComprobarNumerosSeleccionados()) {
					this.setTint(0x00ff00);
				}
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			});

		this.textoBotonComenzar = this.add.text(1000, 160, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.SeleccionarScene_BotonComenzar))
			.setOrigin(0.5, 0)
			.setFontFamily("monospace")
			.setFontSize(26)
			.setFill("#fff");
	}

	seleccionarNumero(itemBolaGrilla: Phaser.GameObjects.Sprite, index: number) {
		if (this.listaBolasSeleccionadasIndices.includes(index)) {
			return;
		}

		const eliminarPrimero = this.listaBolasSeleccionadasIndices.length === 5;
		if (eliminarPrimero) {
			this.eliminarBolaSeleccionImagenSinEfectos(null, 0);
			this.listaBolasSeleccionadasIndices.shift();
		}
		this.listaBolasSeleccionadasIndices.push(index);
		const indiceImagen = this.listaBolasSeleccionadasIndices.length - 1;
		this.agregarBolaSeleccionImagen(itemBolaGrilla, indiceImagen);

	}

	eliminarBolaSeleccionadaImagen(index: number) {
		this.puedeEliminarBolaSeleccionada = false;
		const numeroEliminar = this.listaBolasSeleccionadasIndices[index];
		const bolaEliminar = this.listaBolasSeleccionadasImagenes[index];
		const bolaGrilla = <Phaser.GameObjects.Sprite> this.bolasGrupo.getChildren()[numeroEliminar];
		const finalX = bolaGrilla.x - (bolaGrilla.displayWidth / 2);
		const finalY = bolaGrilla.y - (bolaGrilla.displayHeight / 2);
		this.tweens.add({
			targets: bolaEliminar,
			duration: 500,
			displayWidth: 60,
			displayHeight: 60,
			y: finalY,
			x: finalX,
			onStart: () => {
				//
			},
			onComplete: () => {
				this.listaImagenesBrillo[index].destroy();
				this.listaImagenesBrillo.splice(index, 1);
				this.listaBolasSeleccionadasIndices.splice(index, 1);
				this.listaBolasSeleccionadasImagenes[index].destroy();
				this.listaBolasSeleccionadasImagenes.splice(index, 1);
				let inicioX = 275;
				this.listaBolasSeleccionadasImagenes.forEach((item, index) => {
					item.setPosition(inicioX, 0);
					inicioX += 62;
					item.removeAllListeners(Phaser.Input.Events.POINTER_UP);
					item.on(Phaser.Input.Events.POINTER_UP, () => {
						if (this.puedeEliminarBolaSeleccionada) {
							reproducirSonidoBotones(this);
							this.eliminarBolaSeleccionadaImagen(index)
						}
					});
				});
				const imagen = this.add.image(inicioX, 0, Imagenes.CirculoVacio)
					.setOrigin(0, 0)
					.setDisplaySize(60, 60);
				this.listaBolasSeleccionadasImagenes.push(imagen);
				bolaGrilla.alpha = 1;
				this.puedeEliminarBolaSeleccionada = true;
			}
		});
	}

	agregarBolaSeleccionImagen(itemBolaGrilla: Phaser.GameObjects.Sprite, index: number) {
		this.puedeAgregarBolaSeleccionada = false;
		const finX = this.listaBolasSeleccionadasImagenes[index].x + (this.listaBolasSeleccionadasImagenes[index].displayWidth / 2);
		const finy = this.listaBolasSeleccionadasImagenes[index].y + (this.listaBolasSeleccionadasImagenes[index].displayHeight / 2) ;
		const {x: origenX, y: origenY, displayWidth: origenDisplayWidth, displayHeight: origenDisplayHeight} = itemBolaGrilla;

		this.tweens.add({
			targets: itemBolaGrilla,
			duration: 500,
			displayWidth: 60,
			displayHeight: 60,
			y: finy,
			x: finX,
			onStart: () => {
				//
			},
			onComplete: () => {
				itemBolaGrilla.x = origenX;
				itemBolaGrilla.y = origenY;
				itemBolaGrilla.displayWidth = origenDisplayWidth;
				itemBolaGrilla.displayHeight = origenDisplayHeight;
				itemBolaGrilla.alpha = 0.4;
				this.listaBolasSeleccionadasImagenes[index]
					.setTexture(AtlasImagenes.Bolas, AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(this.listaBolasSeleccionadasIndices[index] + 1))
					.setOrigin(0, 0)
					.setDisplaySize(60, 60)
					.setInteractive({ useHandCursor: true })
					.on(Phaser.Input.Events.POINTER_UP, () => {
						if (this.puedeEliminarBolaSeleccionada) {
							reproducirSonidoBotones(this);
							this.eliminarBolaSeleccionadaImagen(index)
						}
					});
				this.puedeAgregarBolaSeleccionada = true;
				// this.tweens.add({
				// 	targets: this.bola,
				// 	alpha: 1,
				// 	displayWidth: 90,
				// 	displayHeight: 90,
				// 	duration: 200,
				// 	y: 300,
				// 	onStart: () => {
				// 		// this.bola.y = !incrementar ? 200 : 400;
				// 		this.bola.y = !incrementar ? 230 : 370;
				// 	}
				// });
			}
		});
		// const lista = <Phaser.GameObjects.Sprite[]> this.bolasGrupo.getChildren().filter((x, y) => listaBolas.includes(y));
		// const efecto: Phaser.GameObjects.Image[] = [];
		// lista.forEach(item => efecto.push(this.escena.add.image(item.x, item.y, Imagenes.Brillo).setDisplaySize(150, 150)));


		const efecto = this.add.image(origenX, origenY, Imagenes.Brillo).setDisplaySize(120, 120);
		this.listaImagenesBrillo.push(efecto);
		this.tweens.add({
			targets: efecto,
			alpha: 0.7,
			duration: 2000,
			yoyo: true,
			repeat: -1,
			onStart: () => {
				efecto.alpha = 0.1
			},
			onComplete: () => {
				//
			}
		});
	}

	// Este evento se usa cuando tiene 5 bolas seleccionadas
	eliminarBolaSeleccionImagenSinEfectos(itemBolaGrilla: Phaser.GameObjects.Sprite, index: number) {
		this.listaImagenesBrillo[index].destroy();
		this.listaImagenesBrillo.splice(index, 1);
		this.listaBolasSeleccionadasImagenes[index].destroy();
		this.listaBolasSeleccionadasImagenes.splice(index, 1);
		let inicioX = 275;
		this.listaBolasSeleccionadasImagenes.forEach((item, index) => {
			item.setPosition(inicioX, 0);
			inicioX += 62;
			item.removeAllListeners(Phaser.Input.Events.POINTER_UP);
			item.on(Phaser.Input.Events.POINTER_UP, () => {
				if (this.puedeEliminarBolaSeleccionada) {
					reproducirSonidoBotones(this);
					this.eliminarBolaSeleccionadaImagen(index)
				}
			});
		});
		const imagen = this.add.image(inicioX, 0, Imagenes.CirculoVacio)
			.setOrigin(0, 0)
			.setDisplaySize(60, 60);
		this.listaBolasSeleccionadasImagenes.push(imagen);
		const numeroEliminar = this.listaBolasSeleccionadasIndices[index];
		const bolaGrilla = <Phaser.GameObjects.Sprite> this.bolasGrupo.getChildren()[numeroEliminar];
		bolaGrilla.alpha = 1;
	}

	seleccionarNumerosAzar() {
		if (!this.puedeEliminarBolaSeleccionada || !this.puedeAgregarBolaSeleccionada) {
			return;
		}
		this.puedeEliminarBolaSeleccionada = false;
		this.puedeAgregarBolaSeleccionada = false;
		this.listaImagenesBrillo.forEach(item => item.destroy());
		this.listaImagenesBrillo = [];
		(<Phaser.GameObjects.Sprite[]> this.bolasGrupo.getChildren()).filter((item, index) => this.listaBolasSeleccionadasIndices.includes(index))
			.forEach(item => {
				item.alpha = 1;
			});
		const listaNumeros = numerosAzar();
		this.listaBolasSeleccionadasIndices = listaNumeros.map(x => x - 1);
		const finy = this.listaBolasSeleccionadasImagenes[0].y + (this.listaBolasSeleccionadasImagenes[0].displayHeight / 2) ;
		const listaSeleccionadas = (<Phaser.GameObjects.Sprite[]> this.bolasGrupo.getChildren())
			.map((x, y) => ({ itemGrilla: x, indexGrilla: y}))
			.filter(x => this.listaBolasSeleccionadasIndices.includes(x.indexGrilla));
		const listaSeleccionadasOrdenadas: Phaser.GameObjects.Sprite[] = [];
		this.listaBolasSeleccionadasIndices.forEach(item => {
			listaSeleccionadasOrdenadas.push(listaSeleccionadas.find(x => x.indexGrilla === item).itemGrilla);
		});
		const listaPropiedadesOriginales = listaSeleccionadasOrdenadas.map(item => ({x: item.x, y: item.y, displayWidth: item.displayWidth, displayHeight: item.displayHeight}));
		const destinoX = this.listaBolasSeleccionadasImagenes.map(item => item.x + (item.displayWidth / 2));

		this.tweens.add({
			targets: listaSeleccionadasOrdenadas,
			duration: 600,
			displayWidth: 60,
			displayHeight: 60,
			y: finy,
			angle: 360,
			x: {
				getEnd: (target, key, value, indexTweens) => {
					return destinoX[indexTweens];
				}
			},
			onStart: () => {
				//
			},
			onComplete: () => {
				listaSeleccionadasOrdenadas.forEach((itemBolaGrilla, index) => {
					itemBolaGrilla.angle = 0;
					itemBolaGrilla.x = listaPropiedadesOriginales[index].x;
					itemBolaGrilla.y = listaPropiedadesOriginales[index].y;
					itemBolaGrilla.displayWidth = listaPropiedadesOriginales[index].displayWidth;
					itemBolaGrilla.displayHeight = listaPropiedadesOriginales[index].displayHeight;
					itemBolaGrilla.alpha = 0.4;
					this.listaBolasSeleccionadasImagenes[index]
						.setTexture(AtlasImagenes.Bolas, AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(this.listaBolasSeleccionadasIndices[index] + 1))
						.setOrigin(0, 0)
						.setDisplaySize(60, 60)
						.setInteractive({ useHandCursor: true })
						.on(Phaser.Input.Events.POINTER_UP, () => {
							if (this.puedeEliminarBolaSeleccionada) {
								reproducirSonidoBotones(this);
								this.eliminarBolaSeleccionadaImagen(index)
							}
						});
					const efecto = this.add.image(listaPropiedadesOriginales[index].x, listaPropiedadesOriginales[index].y, Imagenes.Brillo).setDisplaySize(120, 120);
					this.listaImagenesBrillo.push(efecto);
					this.tweens.add({
						targets: efecto,
						alpha: 0.7,
						duration: 2000,
						yoyo: true,
						repeat: -1,
						onStart: () => {
							efecto.alpha = 0.1
						},
						onComplete: () => {
							//
						}
					});
				});
				this.puedeEliminarBolaSeleccionada = true;
				this.puedeAgregarBolaSeleccionada = true;
				// itemBolaGrilla.x = origenX;
				// itemBolaGrilla.y = origenY;
				// itemBolaGrilla.displayWidth = origenDisplayWidth;
				// itemBolaGrilla.displayHeight = origenDisplayHeight;
				// itemBolaGrilla.alpha = 0.4;
				// this.listaBolasSeleccionadasImagenes[index]
				// 	.setTexture(AtlasImagenes.Bolas, AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(this.listaBolasSeleccionadasIndices[index] + 1))
				// 	.setOrigin(0, 0)
				// 	.setDisplaySize(60, 60)
				// 	.setInteractive({ useHandCursor: true })
				// 	.on(Phaser.Input.Events.POINTER_UP, () => {
				// 		if (this.puedeEliminarBolaSeleccionada) {
				// 			reproducirSonidoBotones(this);
				// 			this.eliminarBolaSeleccionadaImagen(index)
				// 		}
				// 	});

			}
		});

	}

	cambioTextosIdioma() {
		this.textoBotonComenzar.setText(ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.SeleccionarScene_BotonComenzar));
		this.textoBotonAleatorio.setText(ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.SeleccionRapidaScene_BotonAleatorio));
		this.textoSusNumeros.setText(ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.SeleccionRapidaScene_SusNumeros));
		this.textoBotonSeleccionNormal.setText(ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.SeleccionarScene_BotonSeleccionRapida));
	}

}