import { Variables, AtlasImagenes, AtlasImagenesDetalles } from "../Enumeraciones/Diccionario";

export class NumeroSeleccionado {
	private escena: Phaser.Scene;
	// private imagenBola: Phaser.GameObjects.Image;

	constructor(escene: Phaser.Scene) {
		this.escena = escene;
		this.crear();
	}

	crear() {
		const listaNumerosSeleccionados = <number[]> this.escena.registry.get(Variables.NumerosSeleccionados);
		// const listaFrames = listaNumerosSeleccionados.map(x => AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(x));
		// this.escena.make.group({
		// 	key: AtlasImagenes.Bolas,
		// 	frame: listaFrames,
		// 	max: 5,
		// 	gridAlign: {
		// 		x: 175,
		// 		y: -20,
		// 		width: 5,
		// 		height: 1,
		// 		cellWidth: 55,
		// 		cellHeight: 55,
		// 	},
		// 	"setScale.x": 0.3,
		// 	"setScale.y": 0.3,
		// 	setOrigin: {originX: 0, originY: 0}
		// });

		for (let i = 0; i < 5; i++) {
			this.escena.add.image(i * 55 + 175, 10, AtlasImagenes.Bolas, AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(listaNumerosSeleccionados[i]))
				.setOrigin(0, 0)
				.setDisplaySize(50, 50);
		}

	}
}
