import SplashScreen from "./SplashScreen";
import { Imagenes, SpriteImagenes, SpriteDetalles, Variables, AtlasImagenes, AtlasImagenesDetalles, Sonidos, SonidosDetalles, Eventos, TexturasManuales } from "./../Enumeraciones/Diccionario";
import { configuracionLineasGanadoras, numerosAzar } from "./../Enumeraciones/exportFunction";
import AyudaEscene from "./AyudaScene";
import { ObtenerTextoMultiIdioma, TextosAplicacion } from "../Enumeraciones/MultiIdioma";
import ConfiguracionesScene from "./ConfiguracionesScene";
import SeleccionEscene from "./SeleccionEscene";
import MainGame from "./MainGame";
import FooterScene from "./FooterScene";
import SeleccionRapidaEscene from "./SeleccionRapidaScene";

export default class Preloader extends Phaser.Scene {
	public static Name = "Preloader";

	public preload(): void {
		this.addProgressBar();

		this.load.path = "assets/";
		for(const value in Imagenes) {
			this.load.image(Imagenes[value], `${Imagenes[value]}.png`);
		}

		for(const value in SpriteImagenes) {
			const spriteDetalle = SpriteDetalles[SpriteImagenes[value]];
			this.load.spritesheet(SpriteImagenes[value], spriteDetalle.NombreArchivo, {
				frameWidth: spriteDetalle.AnchoFrame,
				frameHeight: spriteDetalle.AltoFrame,
				endFrame: spriteDetalle.FrameFinal
			});
		}

		for(const value in AtlasImagenes) {
			const spriteDetalle = AtlasImagenesDetalles[AtlasImagenes[value]];
			this.load.atlas(AtlasImagenes[value], spriteDetalle.NombreArchivoImagen, spriteDetalle.NombreArchivoAtlas);
		}

		for(const value in Sonidos) {
			this.load.audio(Sonidos[value], SonidosDetalles[Sonidos[value]].NombreArchivo);
		}
	}

	public create(): void {
		this.anims.create({
			key: 'explotar',
			frames: this.anims.generateFrameNumbers(SpriteImagenes.Humo, {start: 0, end: 20,} ),
			frameRate: 72
		});
		const listaNumeros = numerosAzar();
		this.registry.set(Variables.NumerosSeleccionados, listaNumeros);
		this.registry.set(Variables.EstrellaSeleccionada, Phaser.Math.Between(1, 15));
		this.registry.set(Variables.MusicaEfectosActivada, true);
		this.registry.set(Variables.Saldo, 10000);
		this.registry.set(Variables.MusicaEfectosVolumen, 30);
		this.registry.set(Variables.IdiomaSeleccionado, 0);
		// this.sound.play(Sonidos.MusicaFondo, { loop: true });
		this.crearTexturas();
		this.crearTexturasConTextoMultiIdioma();
		this.registry.events.on(Eventos.MenuAbrirAyuda, (nombreEscena) => {
			this.scene.launch(AyudaEscene.Name);
		});

		this.registry.events.on(Eventos.MenuAbrirConfiguraciones, (nombreEscena) => {
			this.scene.launch(ConfiguracionesScene.Name);
		});

		this.registry.events.on(Eventos.CambiarIdioma, (nombreEscena) => {
			(<ConfiguracionesScene> this.scene.get(ConfiguracionesScene.Name))?.cambioTextosIdioma();
			(<FooterScene> this.scene.get(FooterScene.Name))?.cambioTextosIdioma();
			if (nombreEscena === SeleccionEscene.Name) {
				const auxSeleccionEscene = (<SeleccionEscene> this.scene.get(SeleccionEscene.Name));
				if (auxSeleccionEscene?.scene.settings.visible) {
					auxSeleccionEscene.cambioTextosIdioma();
				}
			}

			if (nombreEscena === MainGame.Name) {
				const auxMainGameScene = <MainGame> this.scene.get(MainGame.Name);
				if (auxMainGameScene?.scene.settings.visible) {
					auxMainGameScene.cambioTextosIdioma();
				}
			}

			if (nombreEscena === SeleccionRapidaEscene.Name) {
				const auxSeleccionRapidaScene = <SeleccionRapidaEscene> this.scene.get(SeleccionRapidaEscene.Name);
				if (auxSeleccionRapidaScene?.scene.settings.visible) {
					auxSeleccionRapidaScene.cambioTextosIdioma();
				}
			}

			this.crearTexturasConTextoMultiIdioma();
		});

		this.scene.start(SplashScreen.Name);
	}

	public update(): void {
		// preload handles updates to the progress bar, so nothing should be needed here.
	}

	/**
	 * Adds a progress bar to the display, showing the percentage of assets loaded and their name.
	 */
	private addProgressBar(): void {
		const width = this.cameras.main.width;
		const height = this.cameras.main.height;
		/** Customizable. This text color will be used around the progress bar. */
		const outerTextColor = '#ffffff';

		const progressBar = this.add.graphics();
		const progressBox = this.add.graphics();
		progressBox.fillStyle(0x222222, 0.8);
		progressBox.fillRect(width / 4, height / 2 - 30, width / 2, 50);

		const loadingText = this.make.text({
			x: width / 2,
			y: height / 2 - 50,
			text: "Loading...",
			style: {
				font: "20px monospace",
				color: outerTextColor
			}
		});
		loadingText.setOrigin(0.5, 0.5);

		const percentText = this.make.text({
			x: width / 2,
			y: height / 2 - 5,
			text: "0%",
			style: {
				font: "18px monospace",
				color: "#ffffff"
			}
		});
		percentText.setOrigin(0.5, 0.5);

		const assetText = this.make.text({
			x: width / 2,
			y: height / 2 + 50,
			text: "",
			style: {
				font: "18px monospace",
				color: outerTextColor
			}
		});

		assetText.setOrigin(0.5, 0.5);

		this.load.on("progress", (value: number) => {
			percentText.setText(parseInt(value * 100 + "", 10) + "%");
			progressBar.clear();
			progressBar.fillStyle(0xffffff, 1);
			progressBar.fillRect((width / 4) + 10, (height / 2) - 30 + 10, (width / 2 - 10 - 10) * value, 30);
		});

		this.load.on("fileprogress", (file: Phaser.Loader.File) => {
			assetText.setText("Loading asset: " + file.key);
		});

		this.load.on("complete", () => {
			progressBar.destroy();
			progressBox.destroy();
			loadingText.destroy();
			percentText.destroy();
			assetText.destroy();
		});
	}

	private crearTexturas() {
		const texturaFondo = this.make.renderTexture({ width: this.cameras.main.displayWidth, height: this.cameras.main.displayHeight }, false);
		texturaFondo.fill(0xffffff, 0.2, 0, 0, this.cameras.main.displayWidth, this.cameras.main.displayHeight);
		texturaFondo.saveTexture(TexturasManuales.FondoTransparente);
	}

	private crearTexturasConTextoMultiIdioma() {
		const anchoTextura = 1140;
		const titulosAlingX = 700;
		const detalleAlingX = 500;
		// const linea = this.add.graphics();
		// linea.lineStyle(8, 0x80effc, 1);
		// linea.visible = false;
		// linea.strokeRectShape(new Phaser.Geom.Rectangle(0, 0, anchoTextura, 400));

		let texturaAyuda1 = this.make.renderTexture({ width: anchoTextura, height: 400 }, false);
		// texturaAyuda1.draw(linea, 0, 0);
		const imagen = this.add.image(0, 0, Imagenes.AyudaComoJugar).setVisible(false).setOrigin(0, 0);
		texturaAyuda1.draw(imagen, 50, 100);

		let text = this.add.text(0, 0, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.AyudaScene_Ayuda1Titulo))
			.setOrigin(0.5, 0)
			.setAlign('center')
			.setFont('Arial Black')
			.setFontSize(24)
			.setFontStyle('bold')
			.setStroke('#000000', 6)
			.setFill('#7dceff');
		texturaAyuda1.draw(text, titulosAlingX, 0);
		let textoAux = this.add.text(0, 0, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.AyudaScene_Ayuda1Detalle))
			.setAlign('left')
			.setFont('Arial Black')
			.setFontSize(16)
			.setFontStyle('bold')
			.setStroke('#000000', 3)
			.setFill('#ffffff');
		texturaAyuda1.draw(textoAux, detalleAlingX, 50);
		texturaAyuda1.saveTexture(TexturasManuales.AyudaComoJugar);

		texturaAyuda1 = this.make.renderTexture({ width: anchoTextura, height: 400 }, false);
		// texturaAyuda1.draw(linea, 0, 0);
		text = this.add.text(0, 0, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.AyudaScene_Ayuda2Titulo))
			.setOrigin(0.5, 0)
			.setAlign('center')
			.setFont('Arial Black')
			.setFontSize(24)
			.setFontStyle('bold')
			.setStroke('#000000', 6)
			.setFill('#7dceff');
		texturaAyuda1.draw(text, titulosAlingX, 0);
		textoAux = this.add.text(0, 0, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.AyudaScene_Ayuda2Detalle))
			.setAlign('left')
			.setFont('Arial Black')
			.setFontSize(16)
			.setFontStyle('bold')
			.setStroke('#000000', 3)
			.setFill('#ffffff');
		texturaAyuda1.draw(textoAux, detalleAlingX, 50);
		texturaAyuda1.saveTexture(TexturasManuales.AyudaLineas);
		const listaLineasGanadoras = configuracionLineasGanadoras();
		listaLineasGanadoras.forEach(x => {
			//
		});

		texturaAyuda1 = this.make.renderTexture({ width: anchoTextura, height: 400 }, false);
		// texturaAyuda1.draw(linea, 0, 0);
		text = this.add.text(0, 0, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.AyudaScene_Ayuda3Titulo))
			.setOrigin(0.5, 0)
			.setAlign('center')
			.setFont('Arial Black')
			.setFontSize(24)
			.setFontStyle('bold')
			.setStroke('#000000', 6)
			.setFill('#7dceff');
		texturaAyuda1.draw(text, titulosAlingX, 0);
		textoAux = this.add.text(0, 0, ObtenerTextoMultiIdioma(this.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.AyudaScene_Ayuda3Detalle))
			.setAlign('left')
			.setFont('Arial Black')
			.setFontSize(16)
			.setFontStyle('bold')
			.setStroke('#000000', 3)
			.setFill('#ffffff');
		texturaAyuda1.draw(textoAux, detalleAlingX, 50);
		texturaAyuda1.saveTexture(TexturasManuales.AyudaGanarBonus);

	}
}
